.block{
  background: white;
  width: 100%;
  height: 100%;
  &_content{
    max-width: 1170px;
    margin: 0 auto;
    display: flex;
    flex-direction: row;
    img{
      margin: 65px 0 0 35px;
    }
  }
}
.content{
&_text{
  width: 470px;
  height: 380px;
  margin: 120px 0 0 70px;
  h4{
    font-size: 14px;
    margin: 0 0 0 0 ;
    font-weight: 700;
    text-transform: uppercase;
    color: #008ed6;
  }
  img{
    margin: 35px 0 35px 0;
  }
  h1{
    padding: 25px 0 0 0;
    margin: 0 0 0 0 ;
    font-size: 36px;
    font-weight: 600;
    text-transform: none;
    letter-spacing: normal;
    color: #1a1a1a;
  }
  p{
    font-size: 18px;
    font-weight: 400;
    text-transform: none;
    letter-spacing: normal;
    color: #999999;
    margin: 0 0 40px 0 ;
  }
  h3{
    margin: 0 0 20px 0 ;
    padding: 0 0 0 0;
    font-size: 17px;
    font-weight: 400;
    text-transform: none;
    letter-spacing: normal;
    color: #1b1b1b;
    img{
      margin: 0 10px 0 0;
    }
  }
}
}